.logout-btn {
    position: absolute;
    top: 25px;
    right: 25px;
    background-color: #e91e63;
    color: white;
    padding: 8px 12px;
}

.dashboard p {
    text-align: center;
    font-size: 1.2em;
}

.connected-platforms {
    margin: 20px 0;
}

.platform-status {
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 10px;
    background-color: #0f1c3d;
}

.connect-btn {
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    font-size: 1em;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.2s;
}

.connect-btn.youtube {
    background-color: #ff0000;
    color: white;
}

.editor-status {
    font-size: 0.8em;
    font-style: italic;
    margin: -5px 0 10px 0;
    padding: 5px 8px;
    border-radius: 4px;
    opacity: 0.9;
}

.editor-status.youtube {
    background-color: #333;
    color: #ccc;
}

.notification {
    margin-top: 15px;
    padding: 12px;
    border-radius: 5px;
    text-align: center;
    font-weight: bold;
    transition: opacity 0.5s ease-in-out;
}

.notification.success {
    background-color: #28a745;
    color: white;
    border: 1px solid #1e7e34;
}
